@import "defaults";
@import "variables";
@import "typography";
@import "buttons";
@import "controller";
//@import "creatorcontent";




/*
	Temp
*/

	.temp {
		display: none;
	}
	
	#system-status-plus {
		position: fixed;
		z-index: 4000;
		top: 8px;
		text-align: center;
		width: 100px;
		margin-left: -50px;
		left: 50%;
		color: red;
		font-size: 10px;
		.relay {
			color: green;
		}
	}

/*
	General
*/


	body {
		background-color:$c_n;
		&.controller {
			background-color: $c_e2;
		}
		&.welcome-state {
			#welcome {
				display: block;
				.pattern {
					opacity: 1;
				}
			}
		}
	}


/*
	Nav – Top
*/
	
	.nav-top {
		z-index: 1000;
		position: fixed;
		width: 100%;
		height: 80px;
		top: 0;
		//background-color: blue;
		padding: 16px;
		
		.logo {
			position: relative;
			float: left;
			background-image: url(../img/logo-white.svg);
			height: 32px;
			width: 106px;
			margin-top: 8px;
		}
		
		.header {
			color: $c_d2;	
			float: left;
			margin-top: 4px;
		}
	}
	
	
/*
	Nav – Bottom
*/
	
	.nav-bottom {
		position: fixed;
		width: 100%;
		height: 112px;
		bottom: 0;
		background-color: red;
		@include linearGradient($c_black_transparent, $c_black_50);
		z-index: 1000;
		
		.left-trigger {
			position: absolute;
			left: 16px;
			bottom: 16px;
			button {
				float: left;
				//margin-bottom: 32px;
			}	
			.caption {
				color: $c_c1;
				margin-bottom: 8px;
			}
		}
	}	


/*
	Sections
*/
		
	section {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		display: none;
		z-index: 100;
		background-color: $c_e2;
		
		&.overlay-block {
			z-index: 200;
		}
		
		//content 
		.content-notice {
			@include basicfloat();
			z-index: 101;
			padding: 160px 64px;
			padding-top: 256px;
			height: 100%;
			overflow-y: scroll; /* has to be scroll, not auto */
			-webkit-overflow-scrolling: touch; 			
			h1 {
				color: $c_d1;
				margin-bottom: 16px;
			}
			h3 {
				color: $c_c2;
				max-width: 384px;
			}			
		}
		
		//Experiences
		.content-feed{
			@include basicfloat();
			z-index: 101;
			padding: 24px 16px;
			padding-top: 104px;
			height: 100%;
			overflow-y: scroll; /* has to be scroll, not auto */
			-webkit-overflow-scrolling: touch; 			
			color: white;			
		}		
		
		//Patterns
		.pattern {
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top: 0;		
			background-position: 50% center;
			background-size: auto 100%;
			z-index: 100;
			opacity: 0;
			
			&.pattern-welcome {
				background-image: url(../patterns/pattern-welcome.svg);
			}
			&.pattern-confirm {
				background-image: url(../patterns/pattern-confirm.svg);
			}	
			&.pattern-sleeping {
				background-image: url(../patterns/pattern-sleeping.svg);
			}
			&.pattern-loading {
				background-image: url(../patterns/pattern-loading.svg);
			}	
			&.pattern-confirm {
				background-image: url(../patterns/pattern-confirm.svg);
			}
			&.pattern-notice {
				background-image: url(../patterns/pattern-notice.svg);
			}										
			
		}
	}


/*
	Experiencees feed
*/



	#experiences ul {
		@include basicfloat();	
	}




/*
	Playing Controls Wrap
*/

	.playing-controls-wrap-outer {
		position: relative;
		max-width: 768px;
		padding: 0 32px;
		margin: 0 auto;
	}
	
	.playing-controls-wrap {
		position: relative;
		width: 100%;
		background-color: gray;
		display: flex;
		justify-content: space-between;
		
		
		//Visual
		background-color: $c_e3;
		color: $c_d1;
		font-family: 'Grot-Medium';
		padding: 0;
		overflow: hidden;
		width: auto;
		@include buttonshadow();
		@include unselectable();	
		
		//Details
		height: 96px;
		border-radius: 48px;
		.flex-item {
			flex-grow: 1;
			max-width: 224px;
			&.current-experience {
				position: relative;
				min-width: 96px;
				overflow: hidden;
			}
		}
		#current-experience-title {
			position: absolute;
			display: table;
			height: 100%;
			width: auto;
			padding-left: 88px;
			z-index: 100;
			p {
				display: table-cell;
				vertical-align: middle;
				white-space: normal;
				font-size: 20px;
				line-height: 24px;
			}
		}
	}	
	
	.current-experience-image-wrap {
		height: 96px;
		width: 96px;
		position: absolute;
		left: 0;
		top: 0;	
		z-index: 90;
		.icon-play {
			position: absolute;
			width: 24px;
			height: 24px;
			top: 50%;
			left: 50%;
			margin-left: -12px;
			margin-top: -12px;
			background-position: center center;
			background-repeat: no-repeat;
			background-image: url(../icons/icon-play-green.svg);
			z-index: 4;
			display: block;
		}
		.gradient {
			z-index: 3;
			@include linearGradientHorizontal(rgba(36,36,36,0), $c_e3);
			height: 100%;
			width: 100%;
			position: absolute;
			left: 0;
			top: 0;				
		}
		#current-experience-image {
			height: 96px;
			width: 96px;
			position: absolute;
			left: 0;
			top: 0;	
			background-position: center center;
			z-index: 2;
		}		
		.default-image {
			z-index: 1;
		}
	}
	

/*
	Default bizz
	
*/		
	.default-image {
		position: absolute;
		width: 100%;
		height: 100%;
		background-image: url("../img/buzz.gif");
	}

	